<template>
  <div class="customerEdit container">
    <Alert v-if="alert" :message="alert"></Alert>
    <h1 class="page-header">编辑用户</h1>
    <form @submit="updateCustomer">
        <div class="well">
            <h4>用户信息</h4>
            <div class="form-group">
                <label>姓名</label>
                <input type="text" class="form-control" placeholder="name" v-model="customer.name"/>
            </div>
            <div class="form-group">
                <label>电话</label>
                <input type="text" class="form-control" placeholder="phone" v-model="customer.phone"/>
            </div>
            <div class="form-group">
                <label>邮箱</label>
                <input type="text" class="form-control" placeholder="email" v-model="customer.email"/>
            </div>
            <div class="form-group">
                <label>学历</label>
                <input type="text" class="form-control" placeholder="education" v-model="customer.education"/>
            </div>
            <div class="form-group">
                <label>毕业院校</label>
                <input type="text" class="form-control" placeholder="graduationschool" v-model="customer.graduationschool"/>
            </div>
            <div class="form-group">
                <label>职业</label>
                <input type="text" class="form-control" placeholder="profession" v-model="customer.profession" />
            </div>
            <div class="form-group">
                <label>个人简介</label>
                <textarea rows="10" class="form-control" placeholder="profile" v-model="customer.profile"></textarea>
            </div>
            <button type="submit" class="btn btn-primary">确认</button>
        </div>
    </form>
  </div>
</template>

<script>
import axios from "axios";
import Alert from "./Alert";
export default {
  name: "customerEdit",
  data() {
    return {
      customer: {},
      alert: ""
    };
  },
  methods: {
    getCustomers(id) {
      axios.get("/users/" + id).then(response => {
        this.customer = response.data;
      });
    },
    updateCustomer(e) {
      if (!this.customer.name || !this.customer.phone || !this.customer.email) {
        this.alert = "请添加对应的信息";
      } else {
        let newCustomer = {
          name: this.customer.name,
          phone: this.customer.phone,
          email: this.customer.email,
          education: this.customer.education,
          graduationschool: this.customer.graduationschool,
          profession: this.customer.profession,
          profile: this.customer.profile
        };
        axios
          .put("/users/" + this.$route.params.id, newCustomer)
          .then(response => {
            this.$router.push({
              path: "/CustomersWrap/Customers/",
              query: { alert: "用户信息更新成功！" }
            });
          });
      }
      e.preventDefault();
    }
  },
  created() {
    this.getCustomers(this.$route.params.id);
  },
  components: {
    Alert
  }
};
</script>



