<template>
  <div class="alert alert-info">
    <slot name="one">{{msg}}</slot>
    <slot name="two">{{msg2}}</slot>
  </div>
</template>
<script>
export default {
  data() {
    return {
      msg: "主标题",
      msg2: "副标题"
    };
  }
};
</script>


