<template>
    <div class="FindShopsInfo">
        <div class="goodsinfoimg" v-for="(ShopsInfo,index) in ShopsInfos.shopsImage" :key="index">
            <img :src="ShopsInfo" />
        </div>
        <div class="goodsinfofooter">
            <div class="fl">
                <p class="tit">{{ShopsInfos.shopsName}}</p>
                <p class="price">{{ShopsInfos.shopsPriceText}}</p>
            </div>
            <button class="shoppingcar">加入购物车</button>
        </div>
        <div class="tuan" @click="showMask">团</div>
        <transition name="mask">
        <div v-show="isShow">
          <div class="blackbg" @click="closeMask"></div>
              <div class="tuanwrap">
                  <div class="pintuan">
                      <div class="tuantext"><p class="tuanprice">￥1000</p><p class="tuannum">7人团</p><p class="tuanarrow" style="display:block"></p></div>
                      <div class="tuantext"><p class="tuanprice">￥1300</p><p class="tuannum">5人团</p><p class="tuanarrow"></p></div>
                      <div class="tuantext"><p class="tuanprice">￥1600</p><p class="tuannum">3人团</p><p class="tuanarrow"></p></div>
                  </div>
                  <div class="clear"></div>
                  <button class="tuanbtn">我要发起（<span>7人团</span>）</button>
                  <div class="pincheng">拼成后立即为您配送，查看更多请<span class="yibu">移步大厅</span></div>
              </div>
          </div>
        </transition>
    </div>
</template>
<script>
import axios from "axios";
let url = "http://123.56.222.140:6466";
let baseKey = "0bd1be8638251d62d11602bc07d32425";
export default {
  name: "FindShopsInfo",
  data() {
    return {
      ShopsInfos: [],
      isShow: false
    };
  },
  created() {
    axios
      .get(url + "/winehomeapp-user/Shops/FindShopsInfo", {
        params: {
          authKey: baseKey, //用户接口请求令牌
          id: this.$route.params.id
        }
      })
      .then(data => {
        console.log(data);
        this.ShopsInfos = data.data.result;
      });
  },
  methods: {
    showMask() {
      this.isShow = true;
    },
    closeMask() {
      this.isShow = false;
    }
  }
};
</script>
<style scoped>
* {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
body {
  background-color: #fff;
}
.goodsinfoback {
  width: 1em;
  padding: 0.5em;
  position: fixed;
}
.goodsinfoback img {
  width: 1em;
}
.goodsinfoimg {
  text-align: center;
  padding-top: 1em;
}
.goodsinfoimg img {
  width: 70%;
  height: 70%;
}
.goodsinfofooter {
  position: fixed;
  width: 100%;
  left: 0em;
  bottom: 0em;
  background: #fff;
  border-top: 1px solid #e8e8e8;
  padding: 0.3em;
  box-sizing: border-box;
}
.goodsinfofooter .fl {
  float: left;
  width: 70%;
}
.goodsinfofooter .tit {
  font-size: 0.5em;
  font-weight: bold;
}
.goodsinfofooter .price {
  color: #d1463f;
  font-size: 0.45em;
}
.goodsinfofooter .shoppingcar {
  background: #d62722;
  color: #fff;
  font-size: 0.4em;
  padding: 0.4em 0.8em;
  float: right;
  border-radius: 1em;
  border: 0px;
  margin-top: 0.8em;
}
.tuan {
  position: fixed;
  bottom: 10em;
  right: 0.5em;
  width: 2em;
  height: 2em;
  border-radius: 50%;
  background: #ff0000;
  color: #fff;
  text-align: center;
  line-height: 2em;
  font-size: 2em;
}
.tuan img {
  width: 1.3em;
}
.blackbg {
  background: rgba(0, 0, 0, 0.3);
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 111;
  left: 0;
  top: 0;
}
.tuanwrap {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 222;
  background-size: 100% 100%;
  background: #d10909;
}
.pintuan {
  padding-top: 1em;
}
.tuantext {
  float: left;
  width: 33.3%;
  text-align: center;
  line-height: 0.9em;
}
.tuanprice {
  color: #fed545;
  font-size: 0.6em;
}
.tuannum {
  color: #fff;
  font-size: 0.5em;
}
.tuanarrow {
  display: none;
  border-style: solid;
  border-width: 0.5em;
  border-color: transparent transparent #fff;
  width: 0px;
  margin: 0 auto;
}
.tuanarrow img {
  width: 0.6em;
}
.tuanbtn {
  background: #fed545;
  width: 70%;
  height: 2.4em;
  text-align: center;
  margin: 0 auto;
  color: #d72722;
  border: 0;
  border-radius: 5em;
  display: block;
  font-size: 0.6em;
  margin-top: 2em;
}
.clear {
  clear: both;
}
.clear:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
.pincheng {
  font-size: 0.4em;
  color: #fff;
  text-align: center;
  margin-top: 1em;
}
.yibu {
  color: #fed545;
  text-decoration: underline;
}
.tuandown {
  text-align: center;
  margin-top: 0.5em;
}
.tuandown img {
  width: 0.6em;
}
/* 动画效果 */
.mask-enter-active,
.mask-leave-active {
  transition: 2s all;
}
/*进来之前，离开之后*/
.mask-enter,
.mask-leave-to {
  opacity: 0;
}

.mask-enter-to,
.mask-leave {
  opacity: 1;
}
</style>



