import onlineWrap from './components/onlinetranslate/onlineWrap.vue'
import articleWrap from './components/article/articleWrap.vue'
import myresume from './components/myresume.vue'
import todomvc from './components/todomvc.vue'
import myblog from './components/blogs/myblog.vue'
import singBlog from './components/blogs/singBlog.vue'
import editBlog from './components/blogs/editBlog.vue'
import gift from './components/Gift.vue'
import CustomersWrap from './components/vcustomers/CustomersWrap.vue'
import Customers from './components/vcustomers/Customers.vue'
import CustomerDetail from './components/vcustomers/CustomerDetail.vue'
import CustomerEdit from './components/vcustomers/CustomerEdit.vue'
import CustomerAdd from './components/vcustomers/CustomerAdd.vue'
import CustomerAbout from './components/vcustomers/CustomerAbout.vue'
import Game from './components/games/Game.vue'
import FindHallList from './components/wine/FindHallList.vue'
import FindHallInfo from './components/wine/FindHallInfo.vue'
import FindShopsInfo from './components/wine/FindShopsInfo.vue'
import cities from './components/cities/cities.vue'

export default [{
    path: '/',
    component: onlineWrap
  },
  {
    path: '/article',
    component: articleWrap
  },
  {
    path: '/myresume',
    component: myresume
  },
  {
    path: '/todomvc',
    component: todomvc
  },
  {
    path: '/myblog',
    component: myblog
  },
  {
    path: '/blog/:id',
    component: singBlog
  },
  {
    path: '/editBlog/:id',
    component: editBlog
  },
  {
    path: '/CustomersWrap',
    component: CustomersWrap,
    children: [{
        path: '/CustomersWrap/Customers',
        component: Customers
      },
      {
        path: '/CustomersWrap/CustomerDetail/:id',
        component: CustomerDetail
      },
      {
        path: '/CustomersWrap/CustomerEdit/:id',
        component: CustomerEdit
      },
      {
        path: '/CustomersWrap/CustomerAdd',
        component: CustomerAdd
      },
      {
        path: '/CustomersWrap/CustomerAbout',
        component: CustomerAbout
      }
    ]
  },
  {
    path: '/CustomerDetail/:id',
    component: CustomerDetail
  },
  {
    path: '/gift',
    component: gift
  },
  {
    path: '/game',
    component: Game
  },
  {
    path: '/FindHallList',
    component: FindHallList
  },
  {
    path: '/FindHallInfo/:id',
    component: FindHallInfo
  },
  {
    path: '/FindShopsInfo/:id',
    component: FindShopsInfo
  },
  {
    path: '/cities',
    component: cities
  }
]



