<template>
<div class="Gift">
  <div class="tanceng">
    <div class="tit">好礼免费送</div>
    <div class="imglist">
      <ul id="gift_list">
        <li v-for="(gift,index) in gifts" :key="index" v-on:click="addClassFun(index,gift.id)" v-bind:class='{active:index===currentIndex}'>
          <p><img :src="gift.image"><i class="dui"></i></p>
          {{gift.giftName}}</li>
      </ul>
    </div>
    <div>
      <input type="text" placeholder="请输入手机号" id="mobile" class="inp" maxlength="11" onkeyup="this.value=this.value.replace(/\D/g,'')" onafterpaste="this.value=this.value.replace(/\D/g,'')" onblur="this.value=this.value.replace(/\D/g,'')"  v-model.trim="phone">
      <div class="yzmwrap">
        <input type="text" placeholder="请输入验证码" class="inp2" id="code" v-model.trim="code" onkeyup="this.value=this.value.replace(/\D/g,'')" onafterpaste="this.value=this.value.replace(/\D/g,'')" onblur="this.value=this.value.replace(/\D/g,'')" maxlength="6">
        <button @click="GetPhoneCode();" class="time" id="tel_code_btn" :disabled="!show">
         <span v-show="show">{{codebtn}}</span>
         <span v-show="!show" class="count">{{count}} s</span>
     </button>
      </div>
      <div class="tip">{{message}}</div>
      <input type="submit" class="btn" value="领取礼品" @click="btn()"/>
    </div>
  </div>
</div>
</template>
<script>
import axios from 'axios'
import qs from 'qs'
let base_url = "http://123.56.222.140:6499"
const TIME_COUNT = 60;
export default{
  name:"Gift",
  data(){
    return{
        message:"",
        phone:"13503314356",
        code:"",
        gifts:[],
        currentIndex:'',
        giftId:'',
        show: true,
        codebtn:'获取验证码',
        count:'',
        timer:null,
    }
  },
  components:{
   
  },
  methods:{
    addClassFun: function(index,giftId) {  
    this.currentIndex = index;
    this.giftId = giftId
    },
    GetPhoneCode(){
      var myreg=/^[1][3,4,5,7,8,9][0-9]{9}$/;  
      if (!myreg.test(this.phone)) {  
        this.message="请输入正确的手机号"
        return false;
      }else{
        if (!this.timer) {
                this.count = TIME_COUNT;
                this.show = false;
                this.timer = setInterval(() => {
                  if (this.count > 0 && this.count <= TIME_COUNT) {
                    this.count--;
                  } else {
                    this.codebtn ="重新发送"
                    this.show = true;
                    clearInterval(this.timer);
                    this.timer = null;
                  }
                }, 1000)
              } 
        axios.post('/winehomeapp-web/Gift/sendSms',qs.stringify({"phoneNumber":this.phone}),{
         headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(res=>{
          console.log(res)
          if(res.data.code == '0000'){
             this.message="手机验证码发送成功!"
           }else{
             this.message=res.data.message
           }
        })
        this.message=""



        // this.$http.post(base_url+"/winehomeapp-web/Gift/sendSms",{"phoneNumber":this.phone},{
        //    headers: {
        //     'Content-Type': 'application/x-www-form-urlencoded'
        //   }
        // })
        // .then(res=>{
        //   return res.json()
        // })
        // .then(response=>{
        //   console.log(response)
        // })


        // fetch(base_url+"/winehomeapp-web/Gift/sendSms",{"phoneNumber":this.phone})//post不了數據
        // .then(res=>{
        //   return res.json()
        // })
        // .then(response=>{
        //   console.log(response)
        //   if(response.code == '0000'){
        //     this.message="手机验证码发送成功!"
        //   }else{
        //     this.message=response.message
        //   }
        // })
        
// axios 请求拦截
// axios.interceptors.request.use((config) => {
//   if (config.method === 'post') {
//     config.data = qs.stringify(config.data) // 序列化post请求数据
//   }
//   return config
// }, (error) => {
//   return Promise.reject(error)
// })


//post数据发送前需要序列化成  a=1&b=2  这样子
// function queryString(json){
//   var arr=[];
//   for(var name in json){
//     arr.push(name + '=' + json[name]);
//   }
//   return arr.join('&');
// }



      
      }
    },
    btn:function(){
		if(this.giftId==""){
			this.message="请输入选择礼品！";
            return false;
		}
      if (this.code.length == 0) {
          this.message="请输入短信验证码！"
          return false;
      }
        this.$http.post(base_url+"/winehomeapp-web/Gift/ReceiveGift",qs.stringify({"phoneNumber":this.phone,"smsCode":this.code,"giftId":this.giftId}),{
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(res=>{
          console.log(res)
          this.message=res.body.message
        })
      
    }
  },
  created(){
    fetch(base_url+"/winehomeapp-web/Gift/FindGiftList")
    .then(res=>{
      return res.json()
    })
    .then(response=>{
     this.gifts=response.result
    })
  }
}
</script>
<style scoped>
*{margin:0; padding:0;}
a { color:#333; text-decoration:none;outline:none;}
li { list-style:none;}
img,input { border:0px;vertical-align:middle}
*{box-sizing:border-box;}

.tanceng {width:300px;background:#fff; color:#333;padding:26px 10px;font-size:12px; position:relative; margin:20px auto;}
.tanceng .tit { text-align:center; font-size:16px;margin-bottom:20px}
.tanceng .imglist ul { display:flex}
.tanceng .imglist li {flex:1; text-align:center;}
.tanceng .imglist li p { position:relative; width:66px; height:66px;border:3px solid #ccc;}
.tanceng .imglist li img { width:100%; height:100%;}
.tanceng .imglist li:hover p {border:3px solid #d72722;}
.tanceng .imglist li.active p{border:3px solid #d72722;}
.tanceng .imglist li.active p .dui { background:url(../images/dui.png) no-repeat; position:absolute; right:0px; bottom:0px;width:23px; height:23px;}

.tanceng .inp { border:1px solid #ccc; width:80%; margin:10px auto; display:block; height:26px;}
.tanceng .yzmwrap { width:80%; margin:10px auto; display:block; height:26px; position:relative;}
.tanceng .inp2 { border:1px solid #ccc; width:100%; display:block; height:26px;}
.tanceng .time { position:absolute; width:40%; border-left:1px solid #ccc; height:20px; top:3px; right:0px; text-align:center; cursor:pointer; background:none; color:#666;}
.tanceng .obtainyzmd {color:#999}
.tanceng .btn { background:#d72722; width:70%; height:28px; line-height:28px; text-align:center; color:#fff; border-radius:5px; cursor:pointer; margin:0 auto; display:block;}
.tanceng .close {background:#d72722;color:#fff; width:20px; height:20px; text-align:center; color:#fff; line-height:20px; border-radius:50%; position:absolute; right:-10px; top:-10px;}
.tip { color:#d72722; font-size:12px;width:80%; margin:10px auto; height:20px;}

</style>



