<template>
    <div id="vheader">
        <ul>
          <li v-for="(city,index) in cities" :key="index">{{city}}</li>
        </ul>
        <input type="text" v-model.trim="inputcity" />
        <button @click="addcity">添加城市</button>
    </div>
</template>
<script>
export default {
  name: "vheader",
  data() {
    return {
      inputcity: ""
    };
  },
  props: {
    cities: Array
  },
  methods: {
    addcity: function() {
      if (this.inputcity != "") {
        this.$emit("addClick", this.inputcity);
        this.inputcity = "";
      }
    }
  }
};
</script>



