<template>
  <div id="todomvc">
     <section class="todoapp">
			<header class="header">
				<h1>{{title}}</h1>
				<input class="new-todo" placeholder="What needs to be done?" v-model.trim="newTodo" @keyup.enter="addTodo">
			</header>
			<section class="main">
				<input id="toggle-all" class="toggle-all" type="checkbox" v-model="isAll">
				<label for="toggle-all">Mark all as complete</label>
				<ul class="todo-list">
					<!--<li class="completed">
						<div class="view">
							<input class="toggle" type="checkbox" checked>
							<label>Taste JavaScript</label>
							<button class="destroy"></button>
						</div>
						<input class="edit" value="Create a TodoMVC template">
					</li>-->
					<li v-for="(todo,index) in filteredTodos" :class="{completed:todo.completed,editing:todo==editedTodo}" :key="index">
						<div class="view">
							<input class="toggle" type="checkbox" v-model="todo.completed">
							<label @dblclick="editTodo(todo)">{{todo.content}}</label>
							<button class="destroy" @click="removeTodo(index)" ></button>
						</div>
						<input class="edit" v-model="todo.content" v-focus="todo==editedTodo"
						@blur="doneEdit(todo,index)" @keyup.enter="doneEdit(todo,index)" @keyup.esc="cancleEdit(todo,index)"
						>
					</li>
				</ul>
			</section>
			<footer class="footer" style="height:40px" v-show="todos.length">
				<span class="todo-count"><strong>{{remain}}</strong> {{remain>1?'items':'item'}} left</span>
				<ul class="filters">
					<li>
						<a :class="{selected:hashName=='all'}" href="#/">All</a>
					</li>
					<li>
						<a :class="{selected:hashName=='active'}" href="#/active">
						Active</a>
					</li>
					<li>
						<a :class="{selected:hashName=='completed'}" href="#/completed">
						Completed</a>
					</li>
				</ul>
				<button class="clear-completed" @click="clear">Clear completed</button>
			</footer>
		</section>
		<footer class="info">
			<p>Double-click to edit a todo</p>
			<p>Template by <a href="http://sindresorhus.com">Sindre Sorhus</a></p>
			<p>Created by <a href="http://todomvc.com">you</a></p>
			<p>Part of <a href="http://todomvc.com">TodoMVC</a></p>
		</footer>
  </div>
</template>

<script>
import "todomvc-app-css/index.css";
var filters = {
  all(todos) {
    return todos;
  },
  active(todos) {
    return todos.filter(todo => {
      return !todo.completed;
    });
  },
  completed(todos) {
    return todos.filter(todo => {
      return todo.completed;
    });
  }
};
export default {
  name: "todomvc",
  data() {
    return {
      title: "todomvc",
      newTodo: "",
      todos: [
        {
          content: "vue",
          completed: false
        },
        {
          content: "vue-router",
          completed: false
        }
      ],
      editedTodo: null,
      hashName: "all"
    };
  },
  methods: {
    addTodo() {
      if (!this.newTodo) {
        return;
      }
      this.todos.push({
        content: this.newTodo,
        completed: false
      });
      this.newTodo = "";
    },
    removeTodo(index) {
      return this.todos.splice(index, 1);
    },
    editTodo(todo) {
      this.editCache = todo.content;
      this.editedTodo = todo;
    },
    doneEdit(todo, index) {
      this.editedTodo = null;
      if (!todo.content) {
        this.removeTodo(index);
      }
    },
    cancleEdit(todo) {
      this.editedTodo = null;
      todo.content = this.editCache;
    },
    clear() {
      this.todos = filters.active(this.todos);
    },
    hashChange() {
      let hashName = location.hash.replace(/#\/?/, "");
      if (filters[hashName]) {
        this.hashName = hashName;
      } else {
        location.hash = "";
        this.hashName = "all";
      }
    }
  },
  computed: {
    remain() {
      return filters.active(this.todos).length;
    },
    isAll: {
      get() {
        return this.remain === 0;
      },
      set(value) {
        this.todos.forEach(todo => {
          todo.completed = value;
        });
      }
    },
    filteredTodos() {
      return filters[this.hashName](this.todos);
    }
  },
  directives: {
    focus(el, value) {
      if (value) {
        el.focus();
      }
    }
  },
  created() {
    window.addEventListener("hashchange", this.hashChange);
  }
};
</script>



