<template>
  <div class="container">
      <h1>{{title}}</h1>
      <p>分享次数{{shareTime}}</p>
      <p>发表于{{date | moment}}</p>
      <p>{{content}}</p>
      <app-author :authorer="author"></app-author>
      <app-share @shared="myshare($event,11)"></app-share>
  </div>
</template>
<script>
import moment from "moment"; //时间库
import Author from "./Author.vue";
import Share from "./share.vue";
export default {
  data() {
    return {
      title: "震惊！",
      date: new Date(),
      content: "为什么中国人民如此幸福",
      author: "芳芳",
      shareTime: 0
    };
  },
  filters: {
    moment: function(value) {
      return moment(value).format("MMMM Do");
    }
  },
  components: {
    "app-author": Author,
    "app-share": Share
  },
  methods: {
    myshare(event, age) {
      this.shareTime++;
      console.log(event, age);
    }
  }
};
</script>
<style scoped>
* {
  color: red;
}
</style>


