<template>
    <div class="game">
        <div class="header">
            <div class="tip">消息</div>
            <div class="nav">
                <div class="navtit">导航</div>
                <ul>
                    <li><a href="#">aa</a></li>
                    <li><a href="#">aa</a></li>
                    <li><a href="#">aa</a></li>
                </ul>
            </div>
        </div>
        
         <div class="swiper-container">
            <div class="swiper-wrapper banner">
                <div class="swiper-slide" v-for="(ad,index) in ads.data" :key="index"><img :src="ad.linkpic"></div>
                <!-- <div class="swiper-slide">111</div>
                <div class="swiper-slide">222</div>
                <div class="swiper-slide">333</div> -->
            </div> 
            <div class="swiper-pagination"></div>
            <div class="swiper-button-prev"></div>
            <div class="swiper-button-next"></div>
          </div> 




        <div class="gonggao">
            <div class="swiper-container2">
            <div class="swiper-wrapper">
                <div class="swiper-slide" v-for="Tip in Tips.data" :key="Tip.id">{{Tip.title}} {{Tip.ctime}}</div>
            </div> 
          </div>






        </div>
        <div class="xianlu">线路测试</div>
        <div class="wangzhi">
            <ul>
                <li v-for="xian in xianlu.data" :key="xian.id"><span>{{xian.linkurl}} </span> <span class="time">{{xian.ping}}</span> <router-link to="" class="btn">进入游戏</router-link></li>
            </ul>
        </div>
        <div class="xianlu">简易网址</div>
        <div class="wangzhi">
            <ul>
                <li v-for="wz in wangzhi.data" :key="wz.id"><span>{{wz.urlid}}</span> <span class="time">{{wz.ping}}</span> <router-link to="" class="btn">进入游戏</router-link></li>
            </ul>
        </div>
        <div class="gamefooter">
            <a href="#">首页</a><a href="#">公告</a><a href="#">活动</a>
        </div>
    </div>
</template>
<script>
import axios from "axios";
let base_url = "http://fjg.a.wxjtest.cn";
export default {
  name: "game",
  data() {
    return {
      xianlu: [],
      wangzhi: [],
      Tips: [],
      ads: []
    };
  },
  //获取线路
  created() {
    axios
      .get(base_url + "/api.php/Index/getLink")
      .then(res => {
        this.xianlu = res.data;
      })
      //转成数组
      //.then(data => {
      //console.log(data);
      // let xianluArray = [];
      // for (let key in data) {
      //   //data[key].id=key;
      //   xianluArray.push(data[key]);
      // }
      // this.xianlu = xianluArray;
      //});
      .catch(function(error) {
        console.log(error);
      });

    //获取简易网址
    axios.get(base_url + "/api.php/Index/getEasyUrl").then(res => {
      this.wangzhi = res.data;
    });

    //获取公告api.php/Index/getMessage
    axios
      .get(base_url + "/api.php/Index/getMessage")
      .then(res => {
        this.Tips = res.data;
      })
      .then(() => {
        var swiper = new Swiper(".swiper-container2", {
          direction: "vertical",
          autoplay: true,
          loop: true
        });
      });

    //获取广告 api.php/Index/getAd
    //debugger;
    axios
      .get(base_url + "/api.php/Index/getAd")
      .then(res => {
        this.ads = res.data;
      })
      .then(() => {
        var swiper = new Swiper(".swiper-container", {
          //spaceBetween: 30,
          centeredSlides: true,
          autoplay: {
            delay: 3000,
            disableOnInteraction: false
          },
          pagination: {
            el: ".swiper-pagination",
            clickable: true
          },
          navigation: {
            nextEl: ".swiper-button-next",
            prevEl: ".swiper-button-prev"
          },
          loop: true
        });
      });
  }
};
</script>
<style scoped>
* {
  margin: 0;
  padding: 0;
}
body {
  font-family: "Microsoft YaHei";
  font-size: 14px;
  color: #333;
  line-height: 150%;
  background: #f2f2f2;
}
a {
  color: #333;
  text-decoration: none;
  outline: none;
}
li {
  list-style: none;
}
img,
input {
  border: 0px;
  vertical-align: middle;
}
table {
  border-collapse: collapse;
}
.clear {
  clear: both;
}
.clear:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
.ellipsis {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.f12 {
  font-size: 12px;
}
.color6 {
  color: #666;
}
.color9 {
  color: #999;
}
.center {
  text-align: center;
}
* {
  box-sizing: border-box;
}

.game {
  max-width: 640px;
  margin: 50px auto;
  background: #fff;
}
.header {
  height: 50px;
  background: #d90000;
  color: #fff;
  position: relative;
}
.tip {
  color: #fff;
  line-height: 50px;
  padding-left: 10px;
}
.nav {
  position: absolute;
  right: 10px;
  top: 10px;
}
.nav ul {
  display: none;
}
.nav a {
  color: #fff;
}
.banner {
  width: 100%;
  height: 258px;
}
.banner img {
  width: 100%;
  height: 258px;
}
.gonggao {
  height: 40px;
  background: #1b1b1b;
  line-height: 40px;
  color: #fff;
  padding-left: 10px;
  overflow: hidden;
}
.xianlu {
  color: #ff0000;
  padding-left: 20px;
  line-height: 40px;
  font-size: 16px;
}
.wangzhi li {
  background: #dcdcdc;
  height: 40px;
  line-height: 40px;
  border-radius: 10px;
  padding-left: 10px;
  width: 90%;
  margin: 10px auto;
}
.wangzhi .btn {
  float: right;
  background: #da0000;
  border-radius: 10px;
  padding: 0px 10px;
  color: #fff;
  line-height: 40px;
}
.wangzhi .time {
  color: #00b526;
  font-size: 18px;
  margin-left: 20px;
}
.gamefooter {
  background: #da0001;
}
.gamefooter a {
  display: inline-block;
  color: #fff;
  width: 33%;
  text-align: center;
  line-height: 40px;
}
</style>



