<template>
    <div id="marked">
        <textarea class="text" v-model="text"></textarea>
        <div class="newtext" v-html="newtext"></div>
    </div>
</template>
<script>
//npm install marked     marked解析器参考https://zybuluo.com/mdeditor
import marked from "marked";
export default {
  name: "marked",
  data() {
    return {
      text: ""
    };
  },
  computed: {
    newtext: function() {
      //解析左侧的内容
      return marked(this.text);
    }
  }
};
</script>
<style>
#marked {
  width: 1000px;
  margin: 30px auto;
  overflow: hidden;
}
.text,
.newtext {
  float: left;
  width: 50%;
  border: 1px solid #ccc;
  box-sizing: border-box;
  height: 300px;
}
</style>



