<template>
    <div class="FindHallList">
		<div id="ulList">
			<ul class="hallList">
				<li v-for="(hallList,index) in hallLists" :key="index" :id="hallList.id">
					<div class="imgDiv">
						<router-link :to="'/FindHallInfo/'+hallList.id"><img :src="hallList.shareImageUrl" alt="" class="shopsImg"></router-link>
					</div>
					<div class="shopsnameDiv">
						<span class="shopsName">{{hallList.shopsName}}</span><span class="guige">1*4</span>
					</div>
					<div class="groupDiv"><span>{{hallList.groupBuyingDiscount}}</span></div>
					<div class="price_countdown">
						<div class="price">
							<span class="shopsDiscountPrice">￥{{hallList.shopsDiscountPrice}}</span>
							<del class="shopsPrice">￥{{hallList.shopsPrice}}</del>
						</div>
						<div class="countdown">
							<img src="../wine/images/countDown.png" alt="">
							<span>{{hallList.endTime}}</span>
						</div>
					</div>
				</li>
			</ul>
		</div>

	</div>
</template>
<script>
import axios from "axios";
let url = "http://123.56.222.140:6466";
let baseKey = "0bd1be8638251d62d11602bc07d32425";
export default {
  name: "FindHallList",
  data() {
    return {
      hallLists: []
    };
  },
  created() {
    axios
      .get(url + "/winehomeapp-user/GroupBuyingShopsHall/FindHallList", {
        params: {
          authKey: baseKey, //用户接口请求令牌
          shopsCategory: 0, //商品种类1白酒
          page: 1 //分页
        }
      })
      .then(data => {
        //console.log(data);
        this.hallLists = data.data.result.items;
      });
  }
};
</script>
<style scoped>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  line-height: 150%;
}
li {
  list-style: none;
}
.FindHallList {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  margin: 0 auto;
}

.hallList {
  padding-top: 0.5rem;
}
.hallList li {
  background: #ffffff;
  width: 80%;
  border-radius: 0.3rem;
  margin: 0 auto;
  margin-bottom: 0.5rem;
  padding: 0.2em 0.5rem;
}
.imgDiv {
  position: relative;
  text-align: center;
}
.imgDiv .shopsImg {
  width: 100%;
  display: inline-block;
}
.shopsnameDiv {
  color: #333333;
  font-size: 0.3rem;
  padding: 0.3em 0;
}
.shopsnameDiv .shopsName {
  margin-right: 1rem;
}
.groupDiv {
  color: #333333;
  font-size: 0.3rem;
  padding: 0.3em 0;
}
.price_countdown {
  overflow: hidden;
  padding: 0.3em 0;
}
.price {
  float: left;
}
.price .shopsDiscountPrice {
  color: #fed545;
  font-size: 0.4rem;
  margin-right: 0.2rem;
}
.price .shopsPrice {
  color: #999;
  font-size: 0.3rem;
}
.countdown {
  float: right;
  color: #999;
}
.countdown img {
  width: 0.4rem;
  height: 0.4rem;
  vertical-align: middle;
}
</style>



