<template>
    <div class="FindHallInfo">
            <div class="shopsDetail">
                <div class="discountShop">
                <div class="imgDiv">
                    <router-link :to="'/findShopsInfo/'+FindHallInfo.shopsId">
                    <img :src="FindHallInfo.shopsImage" alt="" class="shopsImg">
                    </router-link>
                    </div>
                <div class="shopsnameDiv"><span class="shopsName">{{FindHallInfo.shopsName}}</span><span class="guige">1*4</span></div>
                </div>
                <div class="botDiv">
                <ul>
                    <li>
                    <p class="shopsDiscountPrice">￥{{FindHallInfo.shopsDiscountPrice}}</p>
                    <p>拼团价</p>
                    </li>
                    <li>
                    <p class="discountPrice">￥{{FindHallInfo.discountPrice}}</p>
                    <p>共优惠</p>
                    </li>
                    <li>
                    <p class="shopsPrice">￥{{FindHallInfo.shopsPrice}}</p>
                    <p>原售价</p>
                    </li>
                </ul>
                <div class="itemDiv">
                    <p class="p1">{{FindHallInfo.collageText}}({{FindHallInfo.groupBuyingDiscount}})</p>
                    <p class="p1">成团后立即配送</p>
                    <p class="p2"><img class="clockImg" src="../wine/images/countDown.png" alt=""></p>
                    <p class="p3">本团倒计时</p>
                    <p class="p4" id="209" l="19107">{{FindHallInfo.endTime}}</p>
                </div>
                </div>
            </div>
	</div>
</template>
<script>
import axios from "axios";
let url = "http://123.56.222.140:6466";
let baseKey = "0bd1be8638251d62d11602bc07d32425";
export default {
  name: "FindHallInfo",
  data() {
    return {
      FindHallInfo: []
    };
  },
  created() {
    axios
      .get(url + "/winehomeapp-user/GroupBuyingShopsHall/FindHallInfo", {
        params: {
          authKey: baseKey,
          id: this.$route.params.id
        }
      })
      .then(data => {
        this.FindHallInfo = data.data.result[0];
      });
  }
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  line-height: 150%;
}
li {
  list-style: none;
}
.FindHallInfo {
  margin-top: 0.5rem;
}

.discountShop {
  background: #ff0000;
  padding-top: 0.5rem;
}
.imgDiv {
  position: relative;
  text-align: center;
  width: 60%;
  margin: auto;
  padding: 0.1rem;
}
.imgDiv .shopsImg {
  width: 80%;
  display: inline-block;
  background: #fff;
  border: 0.4em solid #ebebeb;
}

.shopsnameDiv {
  color: #fff;
  font-size: 0.3rem;
  padding: 0.3em 0;
  text-align: center;
  margin-top: 0.3rem;
}
.shopsnameDiv .shopsName {
  margin-right: 1rem;
}
.botDiv {
  width: 100%;
  background: #fff;
  padding-bottom: 1em;
}
.botDiv ul {
  width: 100%;
  display: inline-flex;
  align-items: center;
  padding: 0.5em 0;
}
.botDiv ul li {
  width: 33.3%;
}
.botDiv ul li p:first-child {
  color: #d72722;
  text-align: center;
  font-size: 0.4rem;
  margin-bottom: 0.1rem;
}
.botDiv ul li p:last-child {
  color: #999;
  text-align: center;
  font-size: 0.3rem;
}
.itemDiv {
  text-align: center;
}
.itemDiv .p3 {
  color: #d72722;
  font-size: 0.3rem;
  margin-top: 0.2rem;
  margin-bottom: 0.2rem;
}
.itemDiv p {
  font-size: 0.3rem;
}
.p1 {
  color: #999;
  margin-bottom: 0.1rem;
}
.clockImg {
  height: 1rem;
}
.p4 {
  color: #d72722;
}
</style>



