<template>
    <div>
        <button class="btn btn-primary" @click="share()">点击分享</button>
    </div>
</template>
<script>
export default {
  methods: {
    share() {
      this.$emit("shared", { name: "ff" });
    }
  }
};
</script>
<style>
button {
  border: 1px solid #ccc;
  border-radius: 3px;
  background: #fff;
  padding: 0px 10px;
  cursor: pointer;
  margin-top: 20px;
}
</style>


