<template>
  <div id="singBlog">
     <p>{{blog.title}}</p>
      <p>{{blog.content}}</p>
      <h3>博客分类：</h3>
      <p v-for="category in blog.categories" :key="category">
        {{category}}
      </p>
      <h3>博客作者：</h3><p>{{blog.author}}</p>
      <button @click="deleteblog()">删除</button>
      <router-link :to="'/editBlog/'+id">编辑</router-link>
 </div>
</template>
<script>
import axios from "axios";
export default {
  name: "singBlog",
  data() {
    return {
      id: this.$route.params.id,
      blog: {}
    };
  },
  created() {
    axios.get("/blogs/" + this.id).then(data => {
      this.blog = data.data;
    });
  },
  methods: {
    deleteblog() {
      axios.delete("/blogs/" + this.id).then(response => {
        console.log(response);
        this.$router.push({ path: "/myblog" });
      });
    }
  }
};
</script>



