<template>
  <div id="addblog">
    <form>
      <h2>添加博客</h2>
      <h3>博客标题</h3>
      <input type="text" v-model="blog.title">
      <h3>博客内容</h3>
      <textarea v-model="blog.content"></textarea>
      <br>
      Vue.js<input type="checkbox" value="Vue.js" v-model="blog.categories"/>
      Node.js<input type="checkbox" value="Node.js" v-model="blog.categories"/>
      Reat.js<input type="checkbox" value="Reat.js" v-model="blog.categories"/>
      Angular.js<input type="checkbox" value="Angular.js" v-model="blog.categories"/>
      <h3>作者：</h3>
      <select v-model="blog.author">
        <option  v-for="author in authors" :key="author.value" :rel="author.value">{{author.name}}</option>
      </select>
      <button class="addbtn" @click.prevent="post">添加博客</button>
      </form>
      <div id="showblog" v-theme:column="'narrow'">
        <h2>博客列表</h2>
        <input type="text" v-model="search" placeholder="搜索">
        <div v-for="bloglist in filterBlogs" :key="bloglist.title" class="bloglist">
          <router-link :to="'/blog/'+bloglist.id"><h3  v-rainbow>id:{{bloglist.id}} {{bloglist.title | toUpperCase}}</h3></router-link>
          <p>{{bloglist.content | jiequ}}</p>
        </div>
      </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "addblog",
  data() {
    return {
      blog: {
        title: "",
        content: "",
        categories: [],
        author: "aaa"
      },
      authors: [
        {
          name: "aaa",
          value: 1
        },
        {
          name: "bbb",
          value: 2
        },
        {
          name: "ccc",
          value: 3
        }
      ],
      blogs: [],
      search: ""
    };
  },
  methods: {
    post: function() {
      axios.post("/blogs/", this.blog).then(function() {
        alert("发布成功！");
        location.reload();
      });
    }
  },
  created() {
    axios
      .get("/blogs/")
      .then(function(data) {
        return data.data;
      })
      .then(data => {
        //var blogsArray = [];
        //for (let key in data) {
        //data[key].id = key;
        //  blogsArray.push(data[key]);
        //}
        this.blogs = data;
      });
  },
  computed: {
    filterBlogs: function() {
      return this.blogs.filter(blog => {
        return blog.title.match(this.search);
      });
    }
  },
  filters: {
    toUpperCase(value) {
      return value.toUpperCase();
    }
  },
  directives: {
    //自定义属性
    rainbow: {
      bind(el, binding, vnode) {
        el.style.color =
          "#" +
          Math.random()
            .toString(16)
            .slice(2, 8);
      }
    },
    theme: {
      bind(el, binding, vnode) {
        if (binding.value == "wide") {
          el.style.maxWidth = "1260px";
        } else if (binding.value == "narrow") {
          el.style.maxWidth = "560px";
        }
        if (binding.arg == "column") {
          el.style.background = "#6677cc";
          el.style.padding = "20px";
        }
      }
    }
  }
};
</script>
<style>
.addbtn {
  display: block;
  background: #ff0000;
  border-radius: 5px;
  text-align: center;
  width: 100px;
  height: 40px;
  line-height: 40px;
  color: #fff;
  margin-top: 20px;
}
.bloglist {
  border: 1px solid #ccc;
  background: #eee;
  padding: 10px;
  margin-top: 10px;
}
</style>


