<template>
  <div id="articleWrap">
    <Announcement>
      <p slot="one" v-if="item > 60">我要替换标题</p>
      <p slot="two">这是副标题</p>
    </Announcement>
    <Article></Article>
  </div>
</template>
<script>
import Announcement from './Announcement'
import Article from './Article'

export default{
  name: 'articleWrap',
  data(){
    return{
      item:70,
    }
  },
  components:{
    Announcement,Article,   
  }
}
</script>


