<template>
    <div>
        <div id="myresume"><pre>{{code}}</pre></div>
        <div v-html="styleCode"></div>
    </div>
</template>
<script>
    export default{
        name:'myresume',
        data(){
            return{
                code:'',
                //styleCode:'',
                finacode:`
                /*大家好，我是芳芳*/
               body{ 
                color:#ff0000;
                background:yellow;
                }
                `
            }
        },
        created(){
            var n=0;
            setInterval(()=>{
                this.code=this.finacode.substring(0,n);
                n+=1;
            },100)
        },
        computed:{
           styleCode(){
                return '<style>'+this.code+'</style>'
           }
        }
    }
</script>



