<template>
    <div id="transform">
        <form v-on:submit="btn">
            <input type="text" placeholder="请输入要翻译的内容" v-model="origintext"/>
            <select v-model="language">
                <option value="en">English</option>
                <option value="ja">Janpenese</option>
            </select>
            <input type="submit" value="翻译"/>
        </form>
    </div>
</template>
<script>
export default {
  name:"transform",
  data(){
      return{
          origintext:'',
          language:''
      }
  },
  methods:{
      btn:function(e){
          this.$emit("formsub",this.origintext,this.language);
          e.preventDefault();
      }
  },
  created:function(){
      this.language="en";
  }

}
</script>



