<template>
    <div id="cities">
        <h1>{{msg}}</h1>
        <Vheader @addClick="add" :cities="cities"></Vheader>
        <Marked></Marked>
    </div>
</template>
<script>
import Vheader from "./Vheader.vue";
import Marked from "./marked.vue";
export default {
  data() {
    return {
      msg: "欢迎来到离岛的父子传值",
      cities: ["北京", "上海", "广州"]
    };
  },
  components: {
    Vheader,
    Marked
  },
  methods: {
    add: function(data) {
      this.cities.push(data);
    }
  }
};
</script>



