<template>
  <div class="CustomerDetails container">
  	<router-link to="/CustomersWrap/Customers/" class="btn btn-default">返回</router-link>
    <h1 class="page-header">
    	{{customer.name}}

    	<span class="pull-right">
    		<router-link class="btn btn-primary" :to="'/CustomersWrap/CustomerEdit/'+customer.id">
    			编辑
    		</router-link>
    		<button class="btn btn-danger" @click="deleteCustomer(customer.id)">删除</button>
    	</span>
    </h1>
    <ul class="list-group">
    	<li class="list-group-item">
    		<span> 
    			电话：{{customer.phone}}
    		</span>
    	</li>
    	<li class="list-group-item">
    		<span> 
    			邮箱：{{customer.email}}
    		</span>
    	</li>
    </ul>

    <ul class="list-group">
    	<li class="list-group-item">
    		<span>
    			学历：{{customer.education}}
    		</span>
    	</li>
    	<li class="list-group-item">
    		<span> 
    			毕业院校：{{customer.graduationschool}}
    		</span>
    	</li>

		<li class="list-group-item">
			<span> 
				职业：{{customer.profession}}
			</span>
		</li>
		<li class="list-group-item">
			<span> 
				个人简介：{{customer.profile}}
			</span>
		</li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "CustomerDetails",
  data() {
    return {
      customer: {}
    };
  },
  methods: {
    getCustomers(id) {
      axios.get("/users/" + id).then(response => {
        this.customer = response.data;
      });
    },
    deleteCustomer(id) {
      axios.delete("/users/" + id).then(respons => {
        this.$router.push({
          path: "/CustomersWrap/Customers/",
          query: { alert: "用户删除成功！" }
        });
      });
    }
  },
  created() {
    this.getCustomers(this.$route.params.id);
  }
};
</script>


<style scoped>
</style>



