<template>

  <div class="add container">
      <Alert v-if="alert" :message="alert"></Alert>
    <h1 class="page-header">添加用户</h1>
    <form @submit="customerAdd">
        <div class="well">
            <h4>用户信息</h4>
            <div class="form-group">
                <label>姓名</label>
                <input type="text" class="form-control" placeholder="name" v-model="customer.name"/>
            </div>
            <div class="form-group">
                <label>电话</label>
                <input type="text" class="form-control" placeholder="phone" v-model="customer.phone"/>
            </div>
            <div class="form-group">
                <label>邮箱</label>
                <input type="text" class="form-control" placeholder="email" v-model="customer.email"/>
            </div>
            <div class="form-group">
                <label>学历</label>
                <input type="text" class="form-control" placeholder="education" v-model="customer.education"/>
            </div>
            <div class="form-group">
                <label>毕业院校</label>
                <input type="text" class="form-control" placeholder="graduationschool" v-model="customer.graduationschool"/>
            </div>
            <div class="form-group">
                <label>职业</label>
                <input type="text" class="form-control" placeholder="profession" v-model="customer.profession"/>
            </div>
            <div class="form-group">
                <label>个人简介</label>
                <textarea type="text" rows="10" class="form-control" placeholder="profile" v-model="customer.profile"></textarea>
            </div>
            <button type="submit" class="btn btn-primary">添加</button>
        </div>
    </form>
  </div>
</template>

<script>
import axios from "axios";
import Alert from "./Alert";
export default {
  name: "add",
  data() {
    return {
      customer: {},
      alert: ""
    };
  },
  methods: {
    customerAdd(e) {
      e.preventDefault();
      if (!this.customer.name || !this.customer.phone || !this.customer.email) {
        this.alert = "请写完整";
      } else {
        axios.post("/users/", this.customer).then(response => {
          this.$router.push({
            path: "/CustomersWrap/Customers/",
            query: { alert: "用户信息添加成功！" }
          });
        });
        e.preventDefault();
      }
    }
  },
  components: {
    Alert
  }
};
</script>


