<template>
  <div id="onlineWrap">
    <h1>在线翻译</h1>
    <transform @formsub="formsubde"></transform>
    <transoute :translateText="translateText"></transoute>
  </div>
</template>
<script>
import Transform from './Transform'
import Transoute from './Transoute'

export default{
  name: 'onlineWrap',
  data(){
    return{
      translateText:''
    }
  },
  components:{
    Transform,Transoute,
  },
  methods:{
    formsubde:function(text,language){
      this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20180308T074502Z.3dda2e108bf4f35f.f4d8079dcf8fa97c8cfd7f20a7892acfed394728&lang='+language+'&text='+text)
      .then(function(res){
          this.translateText=res.body.text[0]
      })
    }
  }
}
</script>


