<template>
  <div id="editblog">
    <form v-if="!submited">
      <h2>编辑博客</h2>
      <h3>博客标题</h3>
      <input type="text" v-model="blog.title">
      <h3>博客内容</h3>
      <textarea v-model="blog.content"></textarea>
      <br>
      Vue.js<input type="checkbox" value="Vue.js" v-model="blog.categories"/>
      Node.js<input type="checkbox" value="Node.js" v-model="blog.categories"/>
      Reat.js<input type="checkbox" value="Reat.js" v-model="blog.categories"/>
      Angular.js<input type="checkbox" value="Angular.js" v-model="blog.categories"/>
      <h3>作者：</h3>
      <select v-model="blog.author" >
        <option v-for="author in authors" :key="author">{{author}}</option>
      </select>
      <button class="addbtn" @click.prevent="post">编辑博客</button>
      </form>
      <div v-if="submited">
      <h1>发布成功</h1>
      <h3>博客标题：</h3><p>{{blog.title}}</p>
      <h3>博客内容：</h3><p>{{blog.content}}</p>
      <h3>博客分类：</h3>
      <p v-for="category in blog.categories" :key="category.title">
        {{category}}
      </p>
      <h3>博客作者：</h3><p>{{blog.author}}</p>
      </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "editblog",
  data() {
    return {
      id: this.$route.params.id,
      blog: {},
      authors: ["aaa", "bbb", "ccc"],
      submited: false
    };
  },
  methods: {
    fetchdata() {
      axios.get("/blogs/" + this.id).then(response => {
        this.blog = response.data;
      });
    },
    post: function() {
      axios.put("/blogs/" + this.id, this.blog).then(data => {
        this.submited = true;
      });
    }
  },
  created() {
    this.fetchdata();
  }
};
</script>
<style>
.addbtn {
  display: block;
  background: #ff0000;
  border-radius: 5px;
  text-align: center;
  width: 100px;
  height: 40px;
  line-height: 40px;
  color: #fff;
  margin-top: 20px;
}
.bloglist {
  border: 1px solid #ccc;
  background: #eee;
  padding: 10px;
  margin-top: 10px;
}
</style>


