<template>
    <div class="customers container">
      <Alert v-if="alert" :message="alert"></Alert>
    <input type="text" class="form-control" placeholder="搜索" v-model="search">
    <table class="table table-striped">
        <thead>
            <tr>
                <th>姓名</th>
                <th>电话</th>
                <th>邮箱</th>
                <th></th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(customer,index) in filterCustomers(customers, search)" :key="index">
                <td>{{customer.name}}</td>
                <td>{{customer.phone}}</td>
                <td>{{customer.email}}</td>
                <td><router-link class="btn btn-default" :to="'/CustomersWrap/CustomerDetail/'+customer.id">详情</router-link></td>
            </tr>
        </tbody>
    </table>
    
  </div>
</template>
<script>
import axios from "axios";
import Alert from "./Alert";
export default {
  name: "customers",
  data() {
    return {
      customers: [
        {
          name: "aaa",
          phone: 13503314046,
          email: "123@123.com"
        }
      ],
      search: "",
      alert: ""
    };
  },
  methods: {
    fetchCustomers() {
      axios.get("/users/").then(response => {
        this.customers = response.data;
      });
    },
    filterCustomers(customers, value) {
      return customers.filter(customer => {
        return customer.name.match(value);
      });
    }
  },
  created() {
    if (this.$route.query.alert) {
      this.alert = this.$route.query.alert;
    }
    this.fetchCustomers();
  },
  updated() {
    this.fetchCustomers();
  },
  components: {
    Alert
  }
};
</script>



